:root {
  --white: #ffffff;
  --rich-black: #013243;
  --black: #000000;
  --cyan: #86bfd3;
  --grey: #bdbdbe;
}

:root {
  --font-roboto: "Roboto", sans-serif;
}

body {
  font-family: var(--font-roboto);
}

body {
  display: flex;
  flex-direction: column;
}
body main {
  display: flex;
  flex-direction: row;
  height: 50vh;
}
@media (max-width: 480px) {
  body main {
    flex-direction: column;
    height: 50%;
  }
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100vh;
  font-family: var(--font-roboto);
  background-image: url("/images/Background.png");
}
@media (max-width: 480px) {
  body {
    background: none;
  }
}

fieldset {
  border: none;
}

.login-page .header__title {
  font-size: 2rem;
  font-weight: 700;
}
.login-page .cred label {
  font-size: 1rem;
  font-weight: 700;
}
.login-page .cred input {
  background-color: var(--white);
  border-radius: 7px;
  font-size: medium;
  border: none;
  outline: solid;
  outline-width: 1px;
  outline-color: var(--grey);
}

fieldset {
  margin: 0;
  padding: 0;
}

.login-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  text-align: start;
  padding: 2rem 3rem;
}
.login-page .header {
  padding-bottom: 1rem;
}
.login-page .cred__email {
  padding-bottom: 1rem;
}
.login-page .cred label {
  line-height: 2.5rem;
}
.login-page .cred input {
  display: block;
  height: 3.5rem;
  width: 17rem;
  padding-left: 1rem;
}

.phone {
  background-color: var(--rich-black);
}
.phone__header {
  color: var(--white);
}
.phone__header .htitle {
  font-size: 3rem;
  font-weight: bold;
}
.phone__header .htext {
  color: var(--cyan);
}

.phone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.phone__header {
  padding-top: 3rem;
  padding-bottom: 5rem;
}
.phone__image img {
  height: 45vh;
  width: 83vw;
}

.login-page__t-c {
  padding: 1.5rem 0.1rem;
  display: flex;
  flex-direction: row;
}
.login-page__t-c .txt {
  padding-left: 5px;
  font-size: 1.1rem;
}
.login-page__t-c .txt a {
  color: var(--rich-black);
  font-weight: 700;
}
.login-page__t-c .check {
  padding-right: 3px;
}
.login-page__t-c .check input[type=checkbox] {
  appearance: none;
  background-color: var(--white);
  margin: 0;
  font: inherit;
  width: 1.15em;
  height: 1.15em;
  border: 1px solid var(--grey);
  border-radius: 4.5px;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
}
.login-page__t-c .check input[type=checkbox]::before {
  content: "";
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--rich-black);
}
.login-page__t-c .check input[type=checkbox]:checked::before {
  transform: scale(1);
  transform-origin: bottom left;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}